import React, { useState, useEffect } from "react";
import BackIcon from '../../components/Icon/Back'
import { NavBar, Icon, List, Radio,Modal,Toast } from "../../components/antd";
import intl from "react-intl-universal";
import { Link ,useHistory} from "react-router-dom";
import { connect } from "react-redux";
import { evnChange,delEvn,evn_get_config } from "../../actions/login";
import { FormOutlined, DeleteOutlined } from "@ant-design/icons";
import "./index.css";
const alert = Modal.alert;
// import { }
const RadioItem = Radio.RadioItem;
const ListItem = List.Item;

const PREFIX = process.env.__CLIENT__
  ? window._baseUrl || ""
  : process.env.PREFIX || "";
const CLIENT = process.env.__CLIENT__;
let DOMAIN_PREFIX = CLIENT ? window._baseUrl || "" : PREFIX || "";
if (DOMAIN_PREFIX === undefined) {
  DOMAIN_PREFIX = "";
}

const Evn = ({ evnList, evnValue, evnChange, delEvn,evn_get_config }) => {
  const history = useHistory()

  const [isSet, setIsSet] = useState(false);
  const cancel = () => {
    setIsSet(false);
  };
  const maneger = () => {
    setIsSet(true);
  };
  const clickBandel = (i) => {
    Toast.loading(intl.get("changing", 0, null, true))
    // console.log(i);

    // window.localStorage.setItem("evnValue", i.value);
    // window.localStorage.setItem("evnLabel", i.label);
    evn_get_config(i)
    // evnChange(i);
  };
  const delBandel = (i) => {
    alert(' ', intl.get('delOk'), [
      { text: intl.get('cancel') },
      { text: intl.get('del'), onPress: () => delEvn(i) },
    ])
  }
  const editor = (val) => {
    history.push(`${DOMAIN_PREFIX}/workbench/evnconfig/${val}`)
  }
  return (
    <div className={"evn-content"}>
      <NavBar
        mode="light"
        icon={
          isSet ? (
            <span style={{ color: "#333" }} onClick={cancel}>
              取消
            </span>
          ) : (
            <Link replace to={`${DOMAIN_PREFIX}/workbench/login`} replace>
                {/* <Icon style={{ color: "#333" }} type="left" /> */}
                <BackIcon />
            </Link>
          )
        }
        rightContent={
          isSet ? null : (
            <span onClick={maneger} style={{ color: "#E14C46" }}>
              {intl.get("manege")}
            </span>
          )
        }
      >
        {intl.get("evnChange")}
      </NavBar>
      <div className={'white-space-20'}></div>
      {isSet ? (
        <List>
          {evnList.map((i) => (
            <ListItem
              key={i.value}
              extra={
                i.noEditor ? null : (
                  <span>
                    <span className={'plat-icon editor-icon'} onClick={() => {editor(i.id)}} />{'　'}
                    <span className={'plat-icon del-icon'} onClick={()=>delBandel(i)} />
                  </span>
                )
              }
            >
              {i.label}
            </ListItem>
          ))}
        </List>
      ) : (
        <List>
          {/* {console.log(evnList)} */}
          {evnList.map((i) => (
            <RadioItem
              key={i.value}
              checked={evnValue === i.value}
              onChange={() => clickBandel(i)}
            >
              {i.label}
            </RadioItem>
          ))}
            <ListItem onClick={() => {
              history.push(`${DOMAIN_PREFIX}/workbench/evnset`)
          }}>
              <div className={"text-cen"}><span className={'plat-icon add-icon'} />{intl.get("add")}</div>
          </ListItem>
        </List>
      )}
    </div>
  );
};
export default connect(
  (state) => {
    console.log('env_set_page-evnUrl:',state.loginMessage.evnUrl)
    return{
      evnList: state.loginMessage.evnUrl,
      evnValue: state.loginMessage.evnValue,
    }
  },
  {
    evnChange: evnChange,
    delEvn: delEvn,
    evn_get_config:evn_get_config
  }
)(Evn);
