import {
  LOGIN,
  REGIONCHANGE,
  ENVURLCHANGE,
  DELEVN,
  SAVEURL,
  EDITORUEL,
  NOTICE_NUM,
  INIT_URL_FROM_MTL_STORE,
} from "../../actions/login";
import { TENANT_CHANGE, RESET } from "../../actions/appsAction";
import intl from "react-intl-universal";
// import { fromJS }  from 'immutable'
// console.log(window.localStorage.getItem("evnValue")||"https://build.yyuap.com/")
const DAILY_URL = 'https://build.yyuap.com/mobile-app/rest/v1/mobile/app/workbench/env'
const PRE_URL = 'https://yonbuilder-pre.diwork.com/mobile-app/rest/v1/mobile/app/workbench/env?tenantId=pre'
const PROD_URL = 'https://yonbuilder.diwork.com/mobile-app/rest/v1/mobile/app/workbench/env'
const orginEvnList = [
  {
    label: intl.get("daily"),
    value: DAILY_URL,
    noEditor: true,
    id:'daily'
  },
  {
    label: intl.get("pre"),
    value: PRE_URL,
    noEditor: true,
    id:'pre'
  },
  {
    label: intl.get("prod"),
    value: PROD_URL,
    noEditor: true,
    id:'prod'
  },
]
// window.localStorage.setItem('evnUrl',orginEvnList)
const defaultState = {
  regionCode: "+86",
  userName: window.localStorage.getItem("userName") || "",
  tenantList: window.localStorage.getItem("tenantList")
    ? JSON.parse(window.localStorage.getItem("tenantList"))
    : [],
  userCode: "",
  userEmail: "",
  userId: window.localStorage.getItem("userId") || "",
  userMobile: [],
  tenantId: window.localStorage.getItem("tenantId") || "",
  access_token: window.localStorage.getItem("access_token") || "",
  tenantName: window.localStorage.getItem("tenantName") || "",
  evnUrl:  orginEvnList,
  userAvatar: window.localStorage.getItem("userAvatar") || "",
  evnValue: `${window.location.origin}/mobile-app/rest/v1/mobile/app/workbench/env${window.location.origin === 'https://yonbuilder-pre.diwork.com'?'?tenantId=pre' :''}`,// window.location.origin + '/mobile-app/rest/v1/mobile/app/workbench/env' ,//+ window.location.origin === 'https://yonbuilder-pre.diwork.com'?'?tenantId=pre' :'',
  // evnValue: 'https://build.yyuap.com/mobile-app/rest/v1/mobile/app/workbench/env',
  // evnLabel: window.localStorage.getItem("evnLabel") || intl.get("daily"),
  /*
    window.localStorage.setItem('unMsgRead',unMsgRead)
    window.localStorage.setItem('unReadTodoCount',unReadTodoCount)
  */
  unReadTodoCount: window.localStorage.getItem('unReadTodoCount')?window.localStorage.getItem('unReadTodoCount'):"0",
  unMsgRead: window.localStorage.getItem('unMsgRead')?window.localStorage.getItem('unMsgRead'):"0",
};
export default (state = defaultState, { type, payload }) => {
  switch (type) {
    case INIT_URL_FROM_MTL_STORE:
      console.log('store_INIT_URL_FROM_MTL_STORE:',payload)
      return {
        ...state,
        ...payload
      }
    case RESET:
      // window.localStorage.setItem('evnUrl',JSON.stringify(orginEvnList))
      // window.localStorage.setItem('evnValue',JSON.stringify(orginEvnList))
      window.localStorage.removeItem("wb_at");
      window.localStorage.removeItem("notice");
      window.localStorage.removeItem("userId");
      window.localStorage.removeItem("unReadTodoCount");
      window.localStorage.removeItem("unMsgRead");
      window.localStorage.setItem("tenantList",JSON.stringify(['logout']));
      return {
        ...state,
        regionCode: "+86",
        userName: "",
        tenantList: ['logout'],
        userCode: "",
        userEmail: "",
        userId: "",
        userMobile: [],
        userAvatar: window.local,
        evnValue: `${window.location.origin}/mobile-app/rest/v1/mobile/app/workbench/env${window.location.origin === 'https://yonbuilder-pre.diwork.com'?'?tenantId=pre' :''}`,
        tenantId: "",
        access_token: window.localStorage.getItem("access_token") || "",
        tenantName: window.localStorage.getItem("tenantName") || "",
        unReadTodoCount: window.localStorage.getItem('unReadTodoCount')?window.localStorage.getItem('unReadTodoCount'):"0",
        unMsgRead: window.localStorage.getItem('unMsgRead')?window.localStorage.getItem('unMsgRead'):"0",
      };
    case LOGIN:
      // console.log('payload',payload)
      return {
        ...state,
        ...payload,
      };
    case TENANT_CHANGE:
      return {
        ...state,
        ...payload,
      };
    case REGIONCHANGE:
      return {
        ...state,
        ...payload,
      };
    case ENVURLCHANGE:
      return {
        ...state,
        ...payload,
      };
    case DELEVN: {
      const evnUrl = state.evnUrl.filter((i) => i.id !== payload.id);
      // console.log(evnUrl)
      // window.localStorage.setItem("evnUrl", JSON.stringify(evnUrl));
      mtl.setStorage({
        key:'envUrlInStore',
        data:JSON.stringify(evnUrl),
        success: function() {
          // 成功回调
          // evnChange(i,dispatch)
          // Toast.success(res.data && res.data.message || intl.get('changSuccess'), 1)
        },
        fail: function(err) {
          var message = err.message; // 错误信息
        }
      });
      let evnValue = state.evnValue;
      // evnLabel = state.evnLabel;
      // evnValue: window.localStorage.getItem("evnValue") || "daily",
      // evnLabel: window.localStorage.getItem("evnLabel") || intl.get("daily"),
      if (state.evnValue === payload.value) {
        // window.localStorage.setItem("evnValue", "daily");
        // window.localStorage.setItem("evnLabel", intl.get("daily"));
        evnValue = DAILY_URL;
        // evnLabel = intl.get("daily");
      }
      return {
        ...state,
        evnUrl,
        evnValue,
        // evnLabel,
      };
    }
    case SAVEURL: {
      // console.log(payload)
      const evnUrl = [...state.evnUrl, payload];
      // window.localStorage.setItem("evnUrl", JSON.stringify(evnUrl));
      mtl.setStorage({
        key:'envUrlInStore',
        data:JSON.stringify(evnUrl),
        success: function() {
          // 成功回调
          // evnChange(i,dispatch)
          // Toast.success(res.data && res.data.message || intl.get('changSuccess'), 1)
        },
        fail: function(err) {
          var message = err.message; // 错误信息
        }
      });
      return {
        ...state,
        evnUrl,
      };
    }
    case EDITORUEL: {
      // console.log(payload)
      const evnUrl = state.evnUrl.map((val) => {
        if (val.id === payload.oid) {
          return {
            ...val,
            label: payload.label,
            value: payload.value,
          };
        }
        return val;
      });
      // window.localStorage.setItem('evnUrl',JSON.stringify(evnUrl))
      return {
        ...state,
        evnUrl,
      };
    }
    case NOTICE_NUM: {
      return {
        ...state,
        ...payload
      };
    }
  }
  return state;
};
