"use strict";

define(function () {
  function init(obj) {
    mtlBridge.call("umshare.init", JSON.stringify(obj), obj.callback);
  }

  function doShare(obj) {
    mtlBridge.call("umshare.doShare", JSON.stringify(obj), obj.callback);
  }

  function openShare(obj) {
    mtlBridge.call("umshare.openShare", JSON.stringify(obj), obj.callback);
  }

  return {
    init: init,
    openShare: openShare,
    doShare: doShare
  };
});
