import React from "react";
import BackIcon from '../../components/Icon/Back'
import { NavBar, Icon, List } from "../../components/antd";
import { Link,useHistory } from "react-router-dom";
import intl from "react-intl-universal";
import "./index.css";
import { connect } from "react-redux";
import { regionChange } from '../../actions/login'
const PREFIX = process.env.__CLIENT__
  ? window._baseUrl || ""
  : process.env.PREFIX || "";
const CLIENT = process.env.__CLIENT__;
let DOMAIN_PREFIX = CLIENT ? window._baseUrl || "" : PREFIX || "";
if (DOMAIN_PREFIX === undefined) {
  DOMAIN_PREFIX = "";
}

const Item = List.Item;
const ReginCode = ({regionChange}) => {
const history = useHistory()
  return (
    <div>
      <NavBar
        mode="light"
        icon={
          <Link replace to={`${DOMAIN_PREFIX}/workbench/login`} replace>
            {/* <Icon style={{ color: "#333" }} type="left" /> */}
            <BackIcon />
          </Link>
        }
      >
        {/* 选择国家或地区 */}
        {intl.get("regionCode")}
      </NavBar>
      <List renderHeader={() => ""}>
        {/* <Item extra={<span className={"deff-font"}>+86</span>}>用户名</Item> */}
        {intl.get("regionList").map((item) => {
          return (
            <Item onClick={() => {
              regionChange({ regionCode: item.value })
              history.replace(`${DOMAIN_PREFIX}/workbench/login`)
            }} key={item.value} extra={<span className={"deff-font"}>{item.value}</span>}>{item.label}</Item>
          );
        })}
      </List>
    </div>
  );
};

export default connect(null, {
  regionChange:regionChange
})(ReginCode);
